import './App.css';
import React, {useEffect, useState} from 'react'
// import Dropdown from 'react-dropdown';
import Dropdown from 'react-bootstrap/Dropdown';
import { getSuggestedQuery } from '@testing-library/react';



const App = () => {

  const [query, setQuery] = useState("")
  const [pubchemResults, setPubchemResults] = useState([])
  const [goodRXResults, setGoodRXResults] = useState([])
  const [commonSelection, setCommonSelection] = useState([])
  const [dropdownVisible, setDropdownVisible] = useState(false)

  const commonBeersMeds = [
    "hydrocodone/acetaminophen",
    "gabapentin",
    "tramadol HCl",
    "oxycodone HCl/acetaminophen",
    "alprazolam",
    "escitalopram oxalate",
    "sertraline HCl",
    "duloxetine HCl",
    "citalopram hydrobromide",
    "tizanidine HCl",
    "cyclobenzaprine HCl",
    "zolpidem tartrate",
    "oxycodone HCl",
    "lorazepam",
    "fluoxetine HCl",
    "clonazepam",
    "amitriptyline HCl",
    "quetiapine fumarate",
    "methocarbamol",
    "venlafaxine HCl",
    "pregabalin",
    "doxazosin mesylate",
    "baclofen",
    "diazepam",
    "paroxetine HCl"
  ]

  useEffect(() => {

    if (query.length > 2) {

      // pubchem
      fetch(`https://pubchem.ncbi.nlm.nih.gov/rest/autocomplete/compound/${query}/json?limit=10`, {})
      .then(response => response.json())
      .then(result => setPubchemResults(result.dictionary_terms.compound))
      .catch(error => console.log('error', error));

      //GoodRX
      fetch(`https://www.goodrx.com/api/v4/search/autocomplete?term=${query}`, {})
      .then(response => response.json())
      .then(result => setGoodRXResults(result.results.filter(drug => drug.type === "DRUG").map(drug => drug.display)))
      .catch(error => console.log('error', error));

  } else {
    setGoodRXResults([])
    setPubchemResults([])
  }
  } ,[query])




  return (
    <div className="App">
      <div style={{display: 'flex'}}>
        <input style={{fontSize: 20, padding: 5, height: 60, width: 330}} type="text" value={query} onChange={e => setQuery(e.target.value)} placeholder="enter medication"/>
        {/* <Dropdown
        options={commonBeersMeds}
        onChange={({value}) => setQuery(value)}
        value={"select common med"}
        className="dropdown"
        /> */}
      </div>
      <div style={{marginTop: 20, display: 'flex', position: 'relative'}}>
        <div className='api-results-box'>
          <h2>Common Beers Meds</h2>

        {commonBeersMeds.map(med => (
              <div style={{padding: 2}}>
              <a
              style={{margin: 10}}
              onClick={() => {
                setQuery(med)
                setDropdownVisible(false)
              }}
              >{med}</a>
              </div>
            ))}

        </div>
        <div className='api-results-box'>
          <h2>Pubchem</h2>
          {pubchemResults.map((result, i) =>
            <p>{result}</p>
            )}
        </div>
        <div className='api-results-box'>
          <h2>GoodRX</h2>
          {goodRXResults.map((result, i) =>
            <p>{result}</p>
            )}
        </div>

      </div>


    </div>
  );
}

export default App;
